.message {
    display: flex;
    flex-direction: column;
    max-width: 462px;
    row-gap: 10px;
    align-self: end;
    padding: 10px;
}

.time {
    align-self: end;
    margin-right: 12px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    min-width: 414px;
    column-gap: 5px;


}

.imageAndText img {
    max-width: 48px;
    max-height: 48px;
}

.text {
    position: relative;
    display: flex;
    flex-direction: column;
    height: auto;
    background-color: #0066CC;
    border-radius: 15px 15px 0 15px;
    border: 20px solid transparent; border-bottom: 20px solid #0066CC;
    color: #ffffff;
    max-width: 414px;
}

